@import "fonts";


//! --------------------------------------------------------------------
//! Переменные ---------------------------------------------------------
//! --------------------------------------------------------------------


$bg: #F6F7FB;


//! --------------------------------------------------------------------
//! Стили --------------------------------------------------------------
//! --------------------------------------------------------------------


*,
*::before,
*::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
body {
    font-family: "Inter";
    position: relative;
    background: $bg;
}
a {
    text-decoration: none;
}
ul {
    list-style: none;
}
input {
    padding: 0;
    margin: 0;
    border: 0;
    border: none;
    outline: none;
}
button {
    padding: 0;
    border: none;
    cursor: pointer;
}
.containers {
    max-width: 1440px;
    padding: 0 165px;
    margin: 0 auto;
    width: 100%;
}
ol, ul {
    padding-left: 0;
}
dl, ol, ul {
    margin-top: 0;
    margin-bottom: 0;
}
p {
    margin-top: 0;
    margin-bottom: 0;
}
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
    margin-bottom: 0;
}
.display-n {
    display: none !important;
}
.wrappers {
    max-width: 1325px;
    width: 100%;
    margin: 0 auto;
    padding: 0 40px;
}
.rotate-180 {
    transition: all 0.5s ease;
    transform: rotate(180deg);
}
.rotate-45 {
    transition: all 0.5s ease;
    transform: rotate(45deg);
}


.menu {
    width: 115px;
    height: 100vh;
    background: #625DEE;
    border-radius: 0px 48px 0px 0px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 0;
    left: 0;
    overflow: hidden;
    &__logo {
        position: absolute;
        top: 27px;
        left: 50%;
        transform: translate(-50%, 0%);
        & img {
            width: 60px;
            height: 60px;
            transition: all 0.5s ease;
            transform-origin: center;
            transform-box: fill-box;
            animation:rotate 3s linear infinite;
        }
    }
    &__list {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        margin: auto 0;
        padding: 50% 0 30px 0;
        overflow: hidden;
        &-items {
            position: relative;
            margin: 0 0 15px 22px;
            background: transparent;
            height: 60px;
            width: 115px;
            padding: 0 0 0 25px;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            cursor: pointer;
            &:last-child {
                margin: 0 0 0 22px;
            }
            & svg {
                width: 25px;
                height: 25px;
            }
            & .menu-icons {
                fill: #fff;
            }
            &-active {
                background: #F5F7FB;
                border-radius: 32px 0px 0px 32px;
                &::before {
                    content: "";
                    position: absolute;
                    top: -50%;
                    left: -22px;
                    background: #625DEE;
                    border-radius: 0 0 30px 0;
                    width: 100%;
                    height: 30px;
                    z-index: 5;
                }
                &::after {
                    content: "";
                    position: absolute;
                    bottom: -50%;
                    left: -22px;
                    background: #625DEE;
                    border-radius: 0 30px 0 0;
                    width: 100%;
                    height: 30px;
                    z-index: 5;
                }
                & .menu-icons {
                    fill: #625DEE;
                }
                & .menu__list-items-add {
                    position: absolute;
                    top: -50%;
                    right: 0;
                    width: 60px;
                    height: 120px;
                    background: #F5F7FB;
                }
            }
        }
    }
}

.t-header__nav {
    padding: 45px 0 30px 115px;
    font-family: Inter;
    & .wrappers {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-title {
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 29px;
        color: #435179;
        & p {
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            color: #435179;
        }
    }
    &-client {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        &-sum {
            display: flex;
            cursor: pointer;
            align-items: center;
            justify-content: flex-start;
            & h3 {
                font-style: normal;
                font-weight: bold;
                font-size: 16px;
                line-height: 19px;
                color: #625DEE;
            }
            & p {
                font-style: normal;
                font-weight: bold;
                font-size: 16px;
                line-height: 19px;
                color: #625DEE;
                margin: 0 0 0 6px;
            }
        }
        &-notif {
            width: 20px;
            height: 26px;
            cursor: pointer;
            position: relative;
            margin: 0 23px 0 63px;
            &::after {
                content: "";
                position: absolute;
                top: -2px;
                right: -2px;
                background: #5447C8;
                width: 11.50px;
                height: 11.50px;
                border-radius: 50%;
            }
            & svg {
                width: 20px;
                height: 26px;
            }
        }
        &-profile {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            cursor: pointer;
            position: relative;
            &-photo {
                background: #C5CEE0;
                width: 35px;
                height: 35px;
                border-radius: 50%;
                overflow: hidden;
                & img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            &-name {
                margin: 0 0 0 11.50px;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;
                & h3 {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 12px;
                    line-height: 16px;
                    color: #222B45;
                }
                & p {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 10px;
                    line-height: 12px;
                    color: #7B91B0;
                }
            }
        }
        &-arrow {
            margin: 0 0 0 25px;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            width: 10px;
            transition: all 0.5s ease;
        }
        &-list {
            position: absolute;
            bottom: calc(-100% - 5px);
            right: 0;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            background: #fff;
            justify-content: flex-start;
            border-radius: 15px 15px 0 0;
        }
        &-btn {
            width: 100%;
            background: #fff;
            padding: 12px 35px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-bottom: 1px solid #EDF2F6;
            &:first-child {
                border-radius: 10px 10px 0 0;
            }
            &:last-child {
                border-radius: 0 0 10px 10px;
                border-bottom: none;
            }
            & p {
                font-style: normal;
                font-weight: 500;
                font-size: 12px;
                line-height: 16px;
                color: #222B45;
            }
        }
    }
}
